if (
  document.querySelector(
    'input[name="input-area"]',
    'input[(name = "input-package")]',
    ".price__dealer",
    ".price__retail",
    ".amount__dealer",
    ".amount__retail",
    ".prod-type__item",
    ".prod-type__item--active",
    "#inputArea",
    "#inputPackage"
  )
) {
  const inputArea = document.querySelector('input[name="input-area"]');
  const inputPackage = document.querySelector('input[name="input-package"]');
  const dealerPrice = document.querySelector(".price__dealer");
  const retailPrice = document.querySelector(".price__retail");
  const dealerAmount = document.querySelector(".amount__dealer");
  const retailAmount = document.querySelector(".amount__retail");

  const dealerPriceStr = dealerPrice.textContent;
  const dealerPriceStrFloat = parseFloat(dealerPriceStr);
  const dealerPriceResult = dealerPriceStrFloat.toFixed(2);
  const dealerPriceFloatNumber = Number(dealerPriceResult);

  const retailPriceStr = retailPrice.textContent;
  const retailPriceStrFloat = parseFloat(retailPriceStr);
  const retailPriceResult = retailPriceStrFloat.toFixed(2);
  const retailPriceFloatNumber = Number(retailPriceResult);

  const horizontalTypeCalc = () => {
    inputArea.addEventListener("input", () => {
      const valueAreaNumber = +inputArea.value;
      const valueAreaStr = (valueAreaNumber / 3.03).toFixed(2);
      const valueAreaResult = Math.ceil(+valueAreaStr);

      inputPackage.value = valueAreaResult;

      const inputAreaValue = Number(inputArea.value);
      dealerAmount.textContent = (
        dealerPriceFloatNumber *
        (inputAreaValue + (inputAreaValue * 5) / 100)
      ).toFixed(2);
      retailAmount.textContent = (
        retailPriceFloatNumber *
        (inputAreaValue + (inputAreaValue * 5) / 100)
      ).toFixed(2);
    });

    inputPackage.addEventListener("input", () => {
      const valuePackageNumber = +inputPackage.value;
      const valuePackageStr = (valuePackageNumber * 3.03).toFixed(2);
      const valuePackageResult = Math.round(+valuePackageStr);

      inputArea.value = valuePackageResult;

      const inputAreaValue = Number(inputArea.value);
      dealerAmount.textContent = (
        dealerPriceFloatNumber *
        (inputAreaValue + (inputAreaValue * 5) / 100)
      ).toFixed(2);
      retailAmount.textContent = (
        retailPriceFloatNumber *
        (inputAreaValue + (inputAreaValue * 5) / 100)
      ).toFixed(2);
    });
  };

  const diagonalTypeCalc = () => {
    inputArea.addEventListener("input", () => {
      const valueAreaNumber = +inputArea.value;
      const valueAreaStr = (valueAreaNumber / 3.03).toFixed(2);
      const valueAreaResult = Math.ceil(+valueAreaStr);

      inputPackage.value = valueAreaResult;

      const inputAreaValue = Number(inputArea.value);
      dealerAmount.textContent = (
        dealerPriceFloatNumber *
        (inputAreaValue + (inputAreaValue * 10) / 100)
      ).toFixed(2);
      retailAmount.textContent = (
        retailPriceFloatNumber *
        (inputAreaValue + (inputAreaValue * 10) / 100)
      ).toFixed(2);
    });

    inputPackage.addEventListener("input", () => {
      const valuePackageNumber = +inputPackage.value;
      const valuePackageStr = (valuePackageNumber * 3.03).toFixed(2);
      const valuePackageResult = Math.round(+valuePackageStr);

      inputArea.value = valuePackageResult;

      const inputAreaValue = Number(inputArea.value);
      dealerAmount.textContent = (
        dealerPriceFloatNumber *
        (inputAreaValue + (inputAreaValue * 10) / 100)
      ).toFixed(2);
      retailAmount.textContent = (
        retailPriceFloatNumber *
        (inputAreaValue + (inputAreaValue * 10) / 100)
      ).toFixed(2);
    });
  };

  const productTypeItem = document.querySelectorAll(".prod-type__item");

  horizontalTypeCalc();

  productTypeItem.forEach((item) => {
    item.addEventListener("click", (e) => {
      productTypeItem.forEach((el) => {
        el.classList.remove("prod-type__item--active");
        item.classList.add("prod-type__item--active");
      });

      const productTypeItemActive = document.querySelector(
        ".prod-type__item--active"
      );

      const productTypeAttrValue = productTypeItemActive?.dataset.layingType;
      console.log(productTypeAttrValue);

      if (productTypeAttrValue === "horizontal") {
        resetValues();
        horizontalTypeCalc();
      } else {
        resetValues();
        diagonalTypeCalc();
      }
    });
  });

  const resetValues = () => {
    inputArea.value = "";
    inputPackage.value = "";
    dealerAmount.textContent = "0.00";
    retailAmount.textContent = "0.00";
  };

  const inputAreaValidation = document.querySelector("#inputArea");
  const inputPackageValidation = document.querySelector("#inputPackage");

  inputAreaValidation.oninput = function () {
    this.value = this.value.substr(0, 4);
  };

  inputPackageValidation.oninput = function () {
    this.value = this.value.substr(0, 3);
  };
}
