import "jquery.fancytree/dist/modules/jquery.fancytree.filter";
import dataTableJsonFile from "../../resources/dataTable.json";
initJsonArrayObjects = dataTableJsonFile.tableData;

const $ = require("jquery");

let newSelectArr = [];
let newChangeArr = [];

const filterBrandInitArray = (newSelectArr) => {
  dataTable
    .clear()
    .rows.add(
      initJsonArrayObjects.filter(function (prop) {
        return newSelectArr.indexOf(prop.brand) !== -1;
      })
    )
    .draw();
};

$(function () {
  $("#tree").fancytree({
    extensions: ["filter"],
    quicksearch: true,
    source: {
      url: "./data.json",
    },

    keydown: function (e, data) {
      if (e.which === 13) {
        data.node.toggleSelected();
        return false;
      }
    },

    select: function (event, data) {
      $("#statusLine").text(
        event.type + ": " + data.node.isSelected() + " " + data.node
      );

      let propTreeSelected = data.node.title;

      if (data.node.isSelected() === true) {
        newSelectArr.push(propTreeSelected);
        return filterBrandInitArray(newSelectArr);
      }

      const newChangeArr = newSelectArr.filter(function (item) {
        return item !== propTreeSelected;
      });

      newSelectArr = newChangeArr;

      filterBrandInitArray(newSelectArr);

      if (newSelectArr.length === 0) {
        return dataTable.clear().rows.add(initJsonArrayObjects).draw();
      }
    },

    filter: {
      autoApply: true,
      autoExpand: true,
      counter: false,
      fuzzy: true,
      hideExpandedCounter: true,
      hideExpanders: true,
      highlight: true,
      leavesOnly: true,
      nodata: "Ничего не нашлось",
      mode: "hide",
    },

    activeVisible: true,
    aria: true,
    autoActivate: true,
    autoCollapse: false,
    autoScroll: true,
    clickFolderMode: 4,
    checkbox: true,
    checkboxAutoHide: undefined,
    debugLevel: 4,
    disabled: false,
    focusOnSelect: false,
    escapeTitles: false,
    generateIds: false,
    idPrefix: "ft_",
    icon: true,
    keyboard: true,
    keyPathSeparator: "/",
    minExpandLevel: 1,
    quicksearch: false,
    selectMode: 3,
    tabindex: "0",
    titlesTabbable: false,
    tooltip: false,
    lazyLoad: true,
    quicksearch: true,
  });

  var tree = $.ui.fancytree.getTree("#tree");

  $("input[name=search-filter-tree]").on("keyup", function (e) {
    var n,
      tree = $.ui.fancytree.getTree(),
      args = "leavesOnly".split(" "),
      opts = {},
      filterFunc = $("#branchMode").is(":checked")
        ? tree.filterBranches
        : tree.filterNodes,
      match = $(this).val();

    if ((e && e.which === $.ui.keyCode.ESCAPE) || $.trim(match) === "") {
      $("button#btnResetSearch").trigger("click");
      return;
    }
    if ($("#regex").is(":checked")) {
      n = filterFunc.call(
        tree,
        function (node) {
          return new RegExp(match, "i").test(node.title);
        },
        opts
      );
    } else {
      n = filterFunc.call(tree, match, opts);
    }
    $("button#btnResetSearch").attr("disabled", false);
    $("span#matches").text(" совпадений: " + n);
  });

  $("button#btnResetSearch")
    .click(function (e) {
      $("input[name=search-filter-tree]").val("");
      $("span#matches").text("");
      tree.clearFilter();
      var extraTable = $("#t-cat").DataTable();
      extraTable.search(this.value).draw();
    })
    .attr("disabled", true);

  $("fieldset input:checkbox").change(function (e) {
    var id = $(this).attr("id"),
      flag = $(this).is(":checked");
    switch (id) {
      case "counter":
      case "hideExpandedCounter":
        tree.options.filter[id] = flag;
        break;
    }
    tree.clearFilter();
    $("input[name=search-filter-tree]").keyup();
  });

  $("#btnToggleExpand").click(function () {
    $("#tree")
      .fancytree("getRootNode")
      .visit(function (node) {
        node.toggleExpanded();
      });
    return false;
  });
});
